var app = getApp();
const common = require('../../../publicjs/common.js');
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue'; //轮播

export default {
	components: {
		uniSwiperDot
	},
	data() {
		return {
			iscollect: false, //检查是否已经被收藏
			//轮播图
			bannerUrl: ['https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg','https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg'],
			current: 0, //轮播
			dotsStyles: {}, //轮播样式

			//tab选项卡
			tabs: ['活动详情','组  队'],
			activeIndex: 0, //当前的选项卡。0：活动详情，1：组队
			sliderOffset: 0,
			sliderLeft: 0,
	
			//活动详情
			// vo: {
			// 	theme: '',
			// 	postUnit: '',
			// 	applyStartTime: '',
			// 	applyDeadline: '',
			// 	crowd: '',
			// 	note: '',
			// },
			
			vo: {
				"id":"00006",
				"theme":"微电影大赛",
				"posters":"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586104414589&di=15e8b9148233e7d8e130ff63cd36a87a&imgtype=0&src=http%3A%2F%2Fwww.hrtv.cn%2Ftemplets%2Fdefault%2F2017new%2Fimage%2Fsmallvideo.jpg",
				"document":"https://cmrs-dev.oss-cn-chengdu.aliyuncs.com/adocment/%E8%81%8C%E4%B8%9A%E7%94%9F%E6%B6%AF%E8%A7%84%E5%88%92%E9%97%AE%E9%A2%98.docx",
				"postUnit":"重庆文理学院",
				"types":"艺术",
				"crowd":"重庆文理学院全校学生",
				"region":"校级",
				"note":"重庆文理学院",
				"city":"重庆",
				"district":"永川区",
				"college":"重庆文理学院",
				"department":"全体学生",
				"applyStartTime":"2020-05-20",
				"applyDeadline":"2020-05-27",
				"openTeam":0
			},
			
			
			activeParams: {
				id: "", //活动参数值
			},
			
			//组队
			teamList: [
				{
					name:'Annine_',
					title:'参加重庆市计算机设计大赛',
					content:'12月17日，位于日内瓦的世界贸 。易组织（总部并不平静当天，WTO对美国  展开贸易政策审议。在会议上，中国对美国今年以来实施的钢铝关税等单边主义和保护主义政策以及美国阻挠上诉。'
				},
				{
					name:'Annine_2',
					title:'报考驾校组队优惠1000元',
					content:'12月17日，位于日内瓦的世界贸 。易组织（总部并不平静当天，WTO对美国  展开贸易政策审议。在会议上，中国对美国今年以来实施的钢铝关税等单边主义和保护主义政策以及美国阻挠上诉。'
				},
			]
		};
	},
	onLoad(option) {
		if(typeof option != "undefined" || option != "") {
			this.activeParams.id = option.url
		}
	},
	onShow() {
		//获取字体
		common.fonts();
		this.getlist(); 
		this.checkCollect(); //检查是否收藏
	},
	methods: {
		//获取页面数据
		getlist() {
			let url = "/activity/detail";
			if(this.activeIndex == 1) {
				url = "";
				return; //记得删除哦
			}
			common
				.requestURL({
					url: url,
					method: 'GET',
					params: {
						id: this.activeParams.id
					}
				})
				.then(res => {
					if(res.code == 200) {
						this.vo = res.data;
						this.bannerUrl = [];
						if(typeof res.data.posters != "undefined") {
							this.bannerUrl = res.data.posters.split("||");
						}
					}
				});
		},
		//检查是否已经收藏
		checkCollect(){
			common.requestURL({
				url: '/activity/collect/check',
				params: {
					id: this.activeParams.id
				},
				method: 'GET',
			}).then( res => {
				console.log(res);
				if(res.code == 200) {
					
				}
			})
		},
		
		//轮播切换
		change: function(e) {
			this.current = e.detail.current;
		},
		//预览文档
		viewDoc: function() {
			uni.downloadFile({
				url: this.vo.document,
				success(res) {
					if (res.statusCode === 200) {
						const filePath = res.tempFilePath;
						uni.openDocument({
							filePath,
							success(res) {
								console.log('打开文档成功');
							}
						});
					}
				}
			});
		},
		//下载文档
		downLoadDoc: function() {
			let doxurl = this.vo.document; //文档路径
			uni.downloadFile({
				url: doxurl,
				success(res) {
					const filePath = res.tempFilePath;
					uni.saveFile({
						tempFilePath: filePath,
						success(res) {
							let savedFilePath = res.savedFilePath;
							
							uni.getSavedFileInfo({
							  filePath: doxurl,
							  success: function (res) {
							    console.log(res);
								
								uni.showToast({
									title: '下载成功,路径为：' + savedFilePath,
									duration: 2000
								});
								
							  },
							  fail(res) {
								console.log(res);
								uni.showToast({
									title: "文件下载失败！"
								})
							  }
							});
							
							
						},
						fail: function(res) {
							console.log(res);
						}
					});
				}
			});
		},

		//收藏
		acollect(e) {
			//检查是否已经收藏
			console.log("点击了收藏按钮")
			console.log(e);
			e.target
			
			// commonn.requestURL({
			//   url: "/activity/collect",
			//   method: "GET",
			//   params: {
			//     id: this.data.vo.id
			//   }
			// }).then(res=>({

			// }))
		},
		
		//分享
		share() {
			
		},

		//选项卡切换
		tabClick(e) {
			this.sliderOffset = e.currentTarget.offsetLeft;
			this.activeIndex = e.currentTarget.id;
		}
	}
};
